@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*, *::after, *::before {
  box-sizing: inherit;
}



.map {
  height: 100vh;
  width: 100vw;
}

.map-container {
  -webkit-transition: 400ms;
  transition: 400ms;
}

.overlay {
  -webkit-filter: blur(3px) grayscale(0.3);
          filter: blur(3px) grayscale(0.3);
}

.spinner-container {
  position: absolute;
  z-index: 123123123123123123;
  left: 0;
  top: 0;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 50px;
  height: 50px;
  margin: 1px;
  border-radius: 50%;
  border: 6px solid;
  border-color: #ff4545 transparent black transparent;
  -webkit-animation: lds-dual-ring 0.8s linear infinite;
          animation: lds-dual-ring 0.8s linear infinite;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1);
  }

  50% {
    -webkit-transform: rotate(180deg) scale(1.2);
            transform: rotate(180deg) scale(1.2);
  }

  100% {
    -webkit-transform: rotate(360deg) scale(1);
            transform: rotate(360deg) scale(1);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1);
  }

  50% {
    -webkit-transform: rotate(180deg) scale(1.2);
            transform: rotate(180deg) scale(1.2);
  }

  100% {
    -webkit-transform: rotate(360deg) scale(1);
            transform: rotate(360deg) scale(1);
  }
}

.toggle {
  margin: 40px;
  display: grid;
  grid-template-columns: auto 1fr;
  cursor: pointer;

  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 123123;
}

.btn-info {
  display: grid;
  align-items: end;
}

.btn-info i {
  font-size: 1.8rem;
  margin: 0;
  color: #222;
  -webkit-transition: 150ms;
  transition: 150ms;
}

.btn-info i:hover {
  color: #444;
}

.onoffswitch {
  position: relative; width: 90px;
  -webkit-user-select:none; -moz-user-select:none; -ms-user-select: none;
}

.onoffswitch-checkbox {
  display: none;
}

.onoffswitch-label {
  display: block; overflow: hidden; cursor: pointer;
  height: 30px; padding: 0; line-height: 30px;
  border: 2px solid #999999; border-radius: 30px;
  background-color: #EEEEEE;
  -webkit-transition: background-color 0.3s ease-in;
  transition: background-color 0.3s ease-in;
}

.onoffswitch-label:before {
  content: "";
  display: block; width: 30px; margin: 0px;
  background: #FFFFFF;
  position: absolute; top: 0; bottom: 0;
  right: 60px;
  border: 2px solid #999999; border-radius: 30px;
  -webkit-transition: all 0.3s ease-in 0s;
  transition: all 0.3s ease-in 0s; 
}

.onoffswitch-checkbox:checked + .onoffswitch-label {
  background-color: #57F2AF;
}

.onoffswitch-checkbox:checked + .onoffswitch-label, .onoffswitch-checkbox:checked + .onoffswitch-label:before {
 border-color: #57F2AF;
}

.onoffswitch-checkbox:checked + .onoffswitch-label:before {
  right: 0px; 
}

.onoffswitch-checkbox:checked +.onoffswitch-label:before {
  border: 1px solid #999;
}

input[type=range].range-slider {
  -webkit-appearance: none;
  width: 100%;
  margin: 7.3px 0;
}

input[type=range].range-slider:focus {
  outline: none;
}

input[type=range].range-slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 11.4px;
  cursor: pointer;
  
  background: #57F2AF;
  border-radius: 5px;
  border: 0.2px solid #010101;
}

input[type=range].range-slider::-webkit-slider-thumb {
  
  border: 1px solid #999;
  height: 26px;
  width: 26px;
  border-radius: 15px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -7.5px;
}

input[type=range].range-slider:focus::-webkit-slider-runnable-track {
  background: #57F2AF;
}

input[type=range].range-slider::-moz-range-track {
  width: 100%;
  height: 11.4px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: rgba(0, 187, 160, 0.68);
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

input[type=range].range-slider::-moz-range-thumb {
  box-shadow: 0.9px 0.9px 1px #000031, 0px 0px 0.9px #00004b;
  border: 1.8px solid rgba(0, 0, 30, 0);
  height: 26px;
  width: 26px;
  border-radius: 15px;
  background: #ffffff;
  cursor: pointer;
}

input[type=range].range-slider::-ms-track {
  width: 100%;
  height: 11.4px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type=range].range-slider::-ms-fill-lower {
  background: rgba(0, 162, 138, 0.68);
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

input[type=range].range-slider::-ms-fill-upper {
  background: rgba(0, 187, 160, 0.68);
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

input[type=range].range-slider::-ms-thumb {
  box-shadow: 0.9px 0.9px 1px #000031, 0px 0px 0.9px #00004b;
  border: 1.8px solid rgba(0, 0, 30, 0);
  height: 26px;
  width: 26px;
  border-radius: 15px;
  background: #ffffff;
  cursor: pointer;
  height: 11.4px;
}

input[type=range].range-slider:focus::-ms-fill-lower {
  background: rgba(0, 187, 160, 0.68);
}

input[type=range].range-slider:focus::-ms-fill-upper {
  background: rgba(0, 212, 182, 0.68);
}

.range-slider {
  background-color: transparent;
}

.range-container {
  opacity: 0;
  -webkit-animation: 300ms range forwards 1 150ms;
          animation: 300ms range forwards 1 150ms;
  text-align: center;
  width: 150%;
  position: relative;
}

.range-container label, .label {
  font-family: "Roboto Slab";
}

.range-container label {
  position: absolute;
  left: 50%;
  top: 0;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  width: 100%;
  text-align: left;
}

@-webkit-keyframes range {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes range {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

:root {
  --border: 1px solid #222;
}

.feed {
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 50px;
  
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 123123;
  -webkit-animation: .6s slideUp forwards 1;
          animation: .6s slideUp forwards 1;
}

.feed, .feed > * {
  width: 40vw;
  max-width: 700px;
}

.feed h1 {
  text-overflow: ellipsis;
  overflow: hidden;
  font-family: "Roboto Slab";
  color: #222;
  margin: 0;
  border: 1px solid #222;
  border: var(--border);
  margin-bottom: 5px;
  background: whitesmoke;
  padding: 6px;
  text-align: center;
  font-weight: 400;
  font-size: 2vh;
  letter-spacing: 6px;
  text-transform: uppercase;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.1);
}

.feed iframe {
  border: none;
  height: 400px;
  max-height: 30vh;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  border: 1px solid #222;
  border: var(--border);
}

@-webkit-keyframes slideUp {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes slideUp {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}


.modal-container {
  height: 100vh;
  width: 100%;
  background: hsla(0, 0%, 0%, 0.5);
  
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1726387126783168723;
}

.modal {
  background: whitesmoke;
  min-width: 600px;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.modal h1, .row, .modal button {
  font-family: "Roboto Slab";
}

.modal h1 {
  text-align: center;
  margin: 0;
  margin-top: 20px;
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: -6px;
}

.row {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  justify-content: center;
  padding: 20px 40px;
}

i {
  padding: 0 15px;
}

.rows {
  padding: 30px 0;
}

.modal button {
  width: 100%;
  font-size: 2rem;
  background: #222;
  color: white;
  border: 0;
  padding: 15px;
  cursor: pointer;
  -webkit-transition: 100ms;
  transition: 100ms;
}


.modal button:hover {
  background: #444;
}

.modal button:active {
  outline: none;
}
